	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/08/syltheide.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"syltheide.jpg: \" /></p>\n<p>We're back! We didn't exactly have ideal beach weather (and we were cut off from the Internet) but we had a good time nonetheless. </p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/08/syltkoerbe.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"syltkoerbe.jpg: \" /></p>\n<p>Pictures from our week at Sylt are <a href=\"http://www.shcon.com/index.php?album=08_2001%2F20010812_sylt&dispsize=512\">here</a>.</p>\n<p><b>FC St. Pauli</b> <a href=\"http://www.bundesliga.de/en/bundesliga/2001/03/01735/nachbericht.shtml?\">lost at home in the rain to Rostock 1-0</a>. They now have 2 points in 3 games for 14th place. They'll likely need 40 points in 34 games to keep from being demoted.</p>","fields":{"title":"Cool and Windy","date":"12 Aug 2001","url":"https://papascott.de/2001-08-12-cool-and-windy/"}}},"pathContext":{"slug":"/2001-08-12-cool-and-windy/","prev":{"fields":{"title":"Back in Black","date":"04 Aug 2001","slug":"/2001-08-04-back-in-black/"}},"next":{"fields":{"title":"Yellow Eyes","date":"13 Aug 2001","slug":"/2001-08-13-yellow-eyes/"}}}}

